@font-face {
  font-family: HomemadeSausage;
  src: url(../fonts/HomemadeSausage.ttf);
}
html {
  font-family: HomemadeSausage;
  font-size: 16px;
  scroll-behavior: smooth;
}

.hr {
  margin-block: 10px;
  width: 80%;
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid gray;
}

.showAndHideMenu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: linear-gradient(0.25turn, #7dc0ff, 20%, #ebf8e1, #616161, #004483);
  padding-right: 3%;
  color: rgb(61, 61, 61);
}
.showAndHideMenu p, .showAndHideMenu button {
  margin-inline: 10px;
}

body main {
  display: flex;
  flex-direction: column;
  justify-items: center;
}

.end {
  text-align: right;
  font-size: 1rem;
  background: linear-gradient(0.25turn, #b4b4b4, 10%, #caf9ff, #5599ff);
  padding-right: 4%;
  color: rgb(0, 0, 0);
  padding-block: 5px;
}

@font-face {
  font-family: fontBall2;
  src: url(../fonts/webdings.ttf);
}
@keyframes spinning {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes opacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.Ball {
  animation-name: spinning;
  border-radius: 80%;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in, ease-out;
  background-image: radial-gradient(#fff700, #ff0000, #afffb6);
}

.BallRunning {
  animation-name: spinning, opacity;
  border-radius: 80%;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  background-image: radial-gradient(#fff700, #ff0000, #afffb6);
}

.Ball1 {
  border-radius: 33px;
  animation-name: spinning;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in, ease-out;
}
.Ball1 img {
  border-radius: 33px;
  box-shadow: inset 0px 0px 7px 2px red, 0px 0px 7px 2px red;
}
.Ball1 img:hover {
  transform: scale(1.5);
}

.BallRunning1 {
  animation-name: spinning, op;
  border-radius: 33px;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
.BallRunning1 img {
  border-radius: 33px;
  box-shadow: inset 0px 0px 7px 2px red, 0px 0px 7px 2px red;
}

.Ball:hover {
  cursor: pointer;
  animation-name: none;
  transform: scale(1.8);
}

.introBall2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.part2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-image: radial-gradient(#ff8765, #ffffff, #97fdff);
}
.part2 h2 {
  text-align: center;
}
.part2 p {
  text-align: center;
  margin-inline: 10%;
  text-justify: distribute;
}

.boardBall1 {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  margin-top: 1%;
  margin-bottom: 2%;
  width: auto;
  height: auto;
  border: 5px double;
  background-image: linear-gradient(#ffffff, #40e291, #ffffff);
  line-height: 70%;
}

.boardBall2 {
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  margin-top: 1%;
  margin-bottom: 2%;
  font-family: fontBall2;
  font-size: 4px;
  font-size: 8px;
  background-image: linear-gradient(#ffffff, #636363, #ffffff);
}

.Air {
  background-color: none;
}

.y {
  color: red;
  background-color: #fff347;
}

.y1 img {
  box-shadow: 0px 0px 2px 2px yellow;
}

.Wall {
  background-color: #ce9980;
}

.Wall1 {
  background-color: none;
}

#en:hover, #pl:hover, #lightTheme:hover, #darkTheme:hover {
  border: 1px solid red;
}

#en:active, #pl:active, #lightTheme:active, #darkTheme:active {
  scale: 1.5;
}

button {
  color: rgb(77, 77, 77);
  cursor: pointer;
}

.button {
  border-radius: 15px;
  font-weight: 700;
  font-size: 1rem;
  background: radial-gradient(white 10%, 70%, #00f7ff);
  width: auto;
  height: auto;
  transition: transform 1s;
  padding: 10px;
  text-align: center;
  border: 2px outset gray;
  box-shadow: 1px 1px 2px 1px black;
}
.button:hover {
  color: black;
  transform: scale(1.05);
  background: white;
}
.button:active {
  color: #8b0000;
  background: rgb(255, 139, 139);
  transform: scale(1.3);
}
.button:disabled {
  cursor: not-allowed;
  background: radial-gradient(white 10%, 70%, gray);
  box-shadow: none;
  color: gray;
}
.button:disabled:hover {
  color: gray;
  transform: none;
}

#show1, #show2, #show3, #show4, #show5, #show6, #show7 {
  background: radial-gradient(white 10%, 70%, gray);
  width: auto;
  height: auto;
  opacity: 1;
  border: 0px;
}
#show1:hover, #show2:hover, #show3:hover, #show4:hover, #show5:hover, #show6:hover, #show7:hover {
  background: white;
}
#show1:active, #show2:active, #show3:active, #show4:active, #show5:active, #show6:active, #show7:active {
  background: rgb(255, 139, 139);
}

.btnX {
  font-size: 1.5rem;
  outline: none;
  border: none;
  background: none;
  position: absolute;
  right: 15px;
  top: 15px;
  cursor: pointer;
}

.reset {
  visibility: hidden;
  font-weight: 700;
  font-size: 1rem;
  background: radial-gradient(white 10%, 70%, #00f7ff);
  transition: transform 1s;
  padding: 10px;
  box-shadow: 1px 1px 2px 1px gray;
  border-radius: 15px;
  border: 3px outset gray;
}

.r-active {
  visibility: visible;
  transform: scale(1);
}
.r-active:hover {
  color: black;
  transform: scale(1.05);
  background: white;
}
.r-active:active {
  color: #8b0000;
  background: rgb(255, 139, 139);
  transform: scale(1.1);
}

.boardChess {
  line-height: 70%;
  border: double 5px black;
  font-size: 1em;
  display: inline-flex;
  line-height: 0%;
}
.boardChess img:hover {
  box-shadow: inset 0px 0px 7px 2px rgb(0, 38, 255), 0px 0px 7px 2px rgb(0, 38, 255);
}

.boardGol {
  margin-block: 1%;
  border: 5px double;
  background-image: linear-gradient(#fbff00, #e562ff, #fbff00);
  line-height: 70%;
}
.boardGol img:hover {
  box-shadow: inset 0px 0px 7px 2px rgb(0, 38, 255), 0px 0px 7px 2px rgb(0, 38, 255);
}
.boardGol img:active {
  scale: 2;
}

@keyframes spinning {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.cell {
  animation-name: spinning;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  border-radius: 33px;
}

.air {
  border-radius: 5px;
}

#p_red {
  color: rgb(141, 0, 0);
  font-weight: 700;
  padding: 5px;
  box-shadow: 0px 0px 1px 1px rgb(141, 0, 0);
}

.menu {
  position: fixed;
  top: 1%;
  z-index: 3;
  display: flex;
  justify-content: center;
  width: 660px;
  border-radius: 10px;
  border-style: 5px outset;
  padding-inline: 0.1%;
}
.menu ol {
  list-style-type: none;
  padding: 0;
  margin: 0;
  font-weight: 700;
  line-height: 2rem;
  text-align: center;
}
.menu ol a {
  text-decoration: none;
  color: rgb(0, 0, 0);
}
.menu ol > li {
  float: center;
  background-image: linear-gradient(white, rgb(111, 221, 255));
  width: 100px;
  margin-left: 2px;
  border-radius: 10px;
  border: 3px outset rgb(155, 155, 155);
  box-shadow: 5px 5px 5px 0px grey;
  z-index: -1;
}
.menu ol > li:first-child {
  margin-left: 0;
}
.menu ol > li:first-child:active {
  background-image: radial-gradient(white 50%, rgb(251, 255, 1));
  transform: scale(1.3);
}
.menu ol > li:hover {
  background-image: linear-gradient(rgb(255, 255, 255), rgb(68, 134, 255));
}
.menu ol > li:hover > a {
  color: rgb(0, 0, 0);
  font-weight: bold;
}
.menu ol > li > a:hover {
  color: rgb(66, 0, 173);
}
.menu ol > li > ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.menu ol > li > ul > li {
  z-index: 4;
  border: 3px outset rgb(155, 155, 155);
  border-radius: 10px;
  background-image: linear-gradient(white, rgb(184, 184, 184), white);
  box-shadow: 5px 5px 5px 0px grey;
}
.menu ol > li > ul > li:hover a {
  color: rgb(0, 0, 0);
  font-weight: bold;
}
.menu ol > li > ul > li:hover {
  background-image: linear-gradient(white, rgb(68, 134, 255), white);
}
.menu ol > li > ul > li:active {
  background-image: radial-gradient(white 50%, rgb(251, 255, 1));
  transform: scale(1.3);
}
.menu ol > li > ul {
  display: none;
}
.menu ol > li:hover > ul {
  display: block;
}
.menu ol > li {
  float: left;
  background-color: #fdd700;
  width: 100px;
  margin-left: 1px;
  height: 2em;
}

#langDiv {
  display: flex;
  justify-content: flex-start;
  z-index: 1;
  margin-top: 10px;
  margin-left: 10px;
}
#langDiv p {
  border: 1px solid white;
  border-radius: 5px;
  padding-inline: 5px;
  cursor: pointer;
  white-space: nowrap;
}

.overl {
  position: absolute;
  top: 0;
  left: 0;
}

.o-active {
  width: 100vw;
  height: 100vh;
  background: white;
  pointer-events: all;
  opacity: 0.5;
  z-index: 1;
}

.part1 {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: center;
  background-image: linear-gradient(white, lightblue, white);
}
.part1.img-container {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.part1 > .content-container {
  margin-left: 10%;
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  justify-items: flex-start;
}
.part1 > .content-container > p {
  font-size: 1rem;
  margin-bottom: 2vh;
}

.img {
  width: 80%;
  height: auto;
  margin-block: 3%;
  display: inline-block;
  position: relative;
  top: 1%;
  left: 25%;
  box-shadow: inset 0px 0px 7px 2px rgb(129, 129, 129), 0px 0px 7px 2px rgb(129, 129, 129);
  border-radius: 10px;
  cursor: pointer;
}
.img:hover {
  opacity: 0.5;
  transform: scaleX(-1);
}
.img:active {
  transform: scale(1.3);
}

.popup {
  visibility: hidden;
  width: 40vw;
  background-color: white;
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) scale(0);
  transform: translate(-50%, -50%) scale(0);
  -webkit-transition: 200ms ease-in-out;
  transition: 200ms ease-in-out;
  -webkit-box-shadow: 0px 0px 7px 1px gray;
  box-shadow: 0px 0px 7px 1px gray;
  z-index: 2;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: left;
  -ms-flex-align: left;
  align-items: left;
  line-height: 1.5;
  padding: 2% 5% 2% 5%;
}
.popup > h2 {
  font-size: 2rem;
}
.popup > p {
  font-size: 1rem;
}
.popup > p > span {
  color: #8b0000;
  font-size: 1.5rem;
  font-weight: 700;
}

.active {
  visibility: visible !important;
  -webkit-transform: translate(-50%, -50%) scale(1);
  transform: translate(-50%, -50%) scale(1);
}

#tableFilters {
  display: flex;
  flex-direction: column;
}

.tableBtns {
  display: flex;
  flex-direction: row;
  white-space: nowrap;
  width: auto;
  align-items: center;
  margin-inline: 2%;
}

.tableContainer {
  display: flex;
  justify-content: flex-start;
  overflow: auto;
}

.table {
  position: sticky;
  left: 0;
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  background: white;
}
.table th, .table td {
  text-align: center;
  border-collapse: collapse;
  border-spacing: 0;
  border: 1px solid black;
}
.table tr:nth-child(even) {
  background-color: #f2f2f2;
}

.partTable {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-image: radial-gradient(#ff8765, #ffffff, #97fdff);
}
.partTable h2 {
  text-align: center;
}
.partTable p {
  text-align: center;
  margin-inline: 10%;
  text-justify: distribute;
}

.intro {
  display: flex;
  flex-direction: column;
  background: radial-gradient(#fff, #e0e0e0, #97fdff, #4e6eff);
  align-items: center;
  margin-top: 1%;
  margin-bottom: 1%;
  margin-inline: 5%;
  box-shadow: inset 0px 0px 5px 1px rgb(129, 129, 129), 0px 0px 5px 1px rgb(129, 129, 129);
  z-index: 1;
  border: none;
  border-radius: 15px;
}
.intro p {
  font-style: italic;
  margin-inline: 3%;
  margin-bottom: 3%;
  font-size: 1rem;
}
.intro > .center {
  display: flex;
  flex-direction: column;
  text-align: justify;
  margin-inline: 3%;
}
.intro > .center h1 {
  text-align: center;
}

#imgMeSmall {
  box-shadow: inset 0px 0px 7px 2px rgb(129, 129, 129), 0px 0px 7px 2px rgb(129, 129, 129);
  border-radius: 10px;
}

.invoiceEnters {
  display: flex;
  flex-direction: row;
  gap: 3px;
}

@media only screen and (max-width: 970px) {
  html {
    font-size: 16px;
  }
  main > div > .menu {
    scale: 0.8;
  }
  .menu {
    border-style: 3px outset;
  }
  .menu ol > li {
    width: 100px;
    padding-inline: 3px;
    border: 2px outset rgb(155, 155, 155);
  }
  .img {
    width: 50%;
    height: auto;
    margin-inline: auto;
    margin-block: 0%;
    transform: scale(0.8);
  }
  .part1 {
    display: flex;
    flex-direction: column;
  }
  .part1 > .content-container {
    margin-inline: auto;
    width: 80%;
    text-align: center;
  }
  .tableBtns {
    display: flex;
    flex-direction: column;
  }
  .tableBtns button {
    margin-bottom: 5px;
  }
  #tableFilters span {
    display: flex;
    flex-direction: column;
  }
  .tableContainer {
    transform: scaleX(1);
  }
  .invoiceEnters {
    flex-direction: column;
  }
  .boardChess {
    text-align: left;
    font-size: 2vw;
  }
}